import React, { useState, useEffect } from 'react';  //eslint-disable-next-line
import Epic from '@vkontakte/vkui/dist/components/Epic/Epic'
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import ReactSwipe from 'react-swipe';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Tabs from '@vkontakte/vkui/dist/components/Tabs/Tabs'
import TabsItem from '@vkontakte/vkui/dist/components/TabsItem/TabsItem'
import Gallery from '@vkontakte/vkui/dist/components/Gallery/Gallery'
import Header from '@vkontakte/vkui/dist/components/Header/Header'
import Card from '@vkontakte/vkui/dist/components/Card/Card'
import Separator from '@vkontakte/vkui/dist/components/Separator/Separator'
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton'
import Icon24MoreHorizontal from '@vkontakte/icons/dist/24/more_horizontal'
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Counter from '@vkontakte/vkui/dist/components/Counter/Counter'
import Footer from '@vkontakte/vkui/dist/components/Footer/Footer'
import Alert from '@vkontakte/vkui/dist/components/Alert/Alert'

import '../css/Task.css'

function isEmpty(object) {
    return JSON.stringify(object) == "{}";
  }
//https://task-today-tpuhack.herokuapp.com
const finishTask = (props,item) =>{
  fetch(`https://task-today-tpuhack.herokuapp.com/end_my_task?id=id${props.id_v}&task_id=${item._id}&category=${item.categori}&url=${props.sign}`)
  .then(response => response.json())
  .then(result =>{
      switch (result){
        case 1 :
          alert("Задание закончено");
          window.location.reload();
        break;
        case 0 :
          alert("Произошла ошибка попробуйте позже");
        break;
      }
  });
  console.log("Finish");
}

const Task = (props) => {
  window.scrollTo(0, 0);
  const cat = ['Программирование','Доставка','Учёба','Рисование','Дизайн','Пение','Готовка','Minecraft','Общение','Прочее'];
 
  const [activeInfo,setActiveInfo]=useState({});
  const [awaitInfo, setAwaitInfo] = useState({});
  const [employerInfo, setemployerInfo] = useState({});
  // const [userInfo, setUserInfo] = useState(props.data);
  const [tabsCount, settabsCount] = useState(props.tabsCount);
  const [activeModal, setActiveModal] = useState(null);
  const [slideIndex, setSlideIndex] = useState(0);
  const [fun,setFun] = useState(null);

  // useEffect(()=>{
  //   async function fetchData2() {
	// 		let response = await fetch(`https://task-today-tpuhack.herokuapp.com/get_employee?id=id${props.id_v}&url=${props.sign}`);
	// 		let result = await response.json();
	// 		if (result == "0") {
	// 		  } else {
	// 			props.setData(result);
	// 		  }
	// 	}
	// 	if (props.data) {
	// 		props.setCountActiveTask(props.data.active_task.length);
	// 		props.setTabsCount(new Array(props.data.active_task.length,props.data.employer_task.length,props.data.await_task.length));
	// 		fetchData2();
	// 	}
  // },[props.activeStory,props.activePanelTask,props.activePanelAllTask,props.data])

  useEffect(() => {
      window.scrollTo(0, 0);
      async function GetData(){
        props.setPopout(<ScreenSpinner size='large' />);
        fetch(`https://task-today-tpuhack.herokuapp.com/get_active_task?id=id${props.id_v}&url=${props.sign}`).then(response => response.json())
        .then(result =>{
          // console.log(result);
          setActiveInfo(result);
        })
      }
      async function GetData2(){
        fetch(`https://task-today-tpuhack.herokuapp.com/get_await_task?id=id${props.id_v}&url=${props.sign}`).then(response => response.json())
        .then(result =>{
          // console.log(result);
          setAwaitInfo(result);
        })
      }
      async function GetData3(){
        fetch(`https://task-today-tpuhack.herokuapp.com/get_employer_task?id=id${props.id_v}&url=${props.sign}`).then(response => response.json())
        .then(result =>{
          // console.log(result);
          setemployerInfo(result);
          props.setPopout(null);
        })
      }
      // async function GetData4(){
      //   fetch("https://task-today-tpuhack.herokuapp.com/get_employee?id=id"+props.id_v).then(response => response.json())
      //   .then(result =>{
      //     // console.log(result);
      //     setUserInfo(result)
      //     // props.setPopout(null);
      //   })
      // }
      GetData();
      GetData2();
      GetData3();
      // GetData4();
      
  }, []);

  var count = 0;
	return(
      <Panel id={props.id}>
            
            <PanelHeader
             right={<PanelHeaderButton><Icon24MoreHorizontal /></PanelHeaderButton>} 
           >Задания</PanelHeader>
           <Tabs className="TabsTask">
             <TabsItem onClick={()=>{setSlideIndex(0); window.scrollTo(0, 0);} } 
                        selected={slideIndex===0?1:null} after={<Counter size="s">{tabsCount&&tabsCount[0]}</Counter>} >Активные</TabsItem>
             <TabsItem onClick={()=>{setSlideIndex(1); window.scrollTo(0, 0);}} 
                        selected={slideIndex==1?1:null} after={<Counter size="s">{tabsCount&&tabsCount[1]}</Counter>}>Мои</TabsItem>
             <TabsItem onClick={()=>{setSlideIndex(2); window.scrollTo(0, 0);}} 
                        selected={slideIndex==2?1:null} after={<Counter size="s">{tabsCount&&tabsCount[2]}</Counter>}>Ждут</TabsItem>
           </Tabs>
           <Separator />
           <Gallery className="GalleryBody" align='center' slideWidth="100%" slideIndex={slideIndex} onChange={(slideIndex1)=> {setSlideIndex(slideIndex1); setTimeout(window.scrollTo(0, 0),5000);}} >
           {/* <FormLayout> */}
           <div className="ListTask">
           {activeInfo && 
                 cat.map((category,index) =>
                 activeInfo && activeInfo[category] && 
                 <Div key={index} style={{padding : 0}}>
                   {activeInfo && activeInfo[category].length != 0 && <Header mode="secondary">{category}</Header> }
                   {activeInfo && activeInfo[category] && activeInfo[category].map((item,index2) => <Div key={index+index2}style={{padding : 0}}>
                   {/* <Cell className="CellBodyTask" indicator={item.deadline} expandable >
                             {item.name}
                           </Cell> */}
                       <Card size="l" mode="shadow" className="CardBody" onClick={()=>{
                                                                    window.open(`https://vk.com/${item.id_employer}`)
                                                                    // props.setDataTransfer(item);
                                                                    // props.setActivePanelTask("Chats");
                       }}>
                           <Cell indicator={item.deadline} expandable >
                             {item.name}
                           </Cell>
                       </Card>
                   </Div>)}
                 </Div>)
               }
               {tabsCount&& tabsCount[0] === 0 && <Footer>Пока пусто</Footer>}
           </div>
           <div className="ListTask">          
               {employerInfo && 
                 cat.map((category,index) =>
                   employerInfo && employerInfo[category] && 
                 <Div key={index}style={{padding : 0}}>
                   {employerInfo && employerInfo[category].length != 0 && <Header mode="secondary">{category}</Header> }
                   {employerInfo && employerInfo[category] && employerInfo[category].map((item,index2) => <Div key={index+index2}style={{padding : 0}}>
                       <Card size="l" mode="shadow" className="CardBody" onClick={()=>{ 
                                                                if (item.show_task != "0"){
                                                                  props.setDataTransfer(item); //console.log(item);
                                                                  props.setActivePanelTask("Candidates")
                                                                }else{
                                                                  props.setPopout(
                                                                    <Alert actionsLayout="vertical"
                                                                      actions={[{
                                                                      title: 'Закончить задание',
                                                                      autoclose: true,
                                                                      mode: 'destructive',
                                                                      action: () => {
                                                                      props.setPopout(<Alert actionsLayout="horizantal"
                                                                        actions={[{
                                                                          title: 'Да',
                                                                          autoclose: true,
                                                                          mode: 'destructive',
                                                                          action: () => {
                                                                            console.log(item);
                                                                            finishTask(props,item)
                                                                          } },
                                                                          {
                                                                            title: 'Нет',
                                                                            autoclose: true,
                                                                            mode: 'cancel'
                                                                          }
                                                                          ]} onClose={() => props.setPopout(null)}>
                                                                            <h2>Вы уверены ?</h2>
                                                                        </Alert>)
                                                                        
                                                                      },
                                                                    }, {
                                                                      title: 'Открать профиль работника',
                                                                      autoclose: true,
                                                                      mode: 'cancel',
                                                                      action: () => {
                                                                        // props.setActivePanelTask("ProfileEmployee");
                                                                        window.open(`https://vk.com/${item.id_employee}`);
                                                                      },
                                                                    },{
                                                                      title: 'Отмена',
                                                                      autoclose: true,
                                                                      mode: 'cancel'
                                                                    }]}
                                                                    onClose={() => props.setPopout(null)}>
                                                                    <h2>Выбирите действие</h2>
                                                                    {/* <p>Вы уверены, что хотите взять этого пользователя на работу?</p> */}
                                                          </Alert>
                                                                  )
                                                                  // alert("Вы уже нанели работника");
                                                                }}}>
                           <Cell indicator={item.deadline} expandable >
                             {item.name}
                           </Cell>
                       </Card>
                   </Div>)}
                 </Div>)
               }
               {tabsCount&& tabsCount[1] === 0 && <Footer>Пока пусто</Footer>}
               <div className="div_dop"></div>
           </div>
           <div className="ListTask">
           {awaitInfo && 
                <div>
                  <Header mode="secondary">
                      Ожидают подтверждения
                  </Header>
                  {
                    cat.map((category,index) =>
                    awaitInfo && awaitInfo[category] && 
                  <Div key={index}style={{padding : 0}}>
                    {/* {awaitInfo && awaitInfo[category].length != 0 && <Header mode="secondary">{category}</Header> } */}
                    {awaitInfo && awaitInfo[category] && awaitInfo[category].map((item,index2) => {
                    if (item.taskIsEnd === "0"){
                      return <Div key={index+index2}style={{padding : 0}}>  
                                <Card size="l" mode="shadow" className="CardBody" >
                                  <Cell indicator={item.deadline} expandable >
                                    {item.name}
                                  </Cell>
                                </Card>
                              </Div>
                    }
                    })}
                  </Div>)
                  }
                  {tabsCount&& (tabsCount[2] === 0) && <Footer className="FooterTask">Пока пусто</Footer>}
                  <Header mode="secondary">
                      Ожидают отзыва
                  </Header>
                  {
                    cat.map((category,index) =>
                    awaitInfo && awaitInfo[category] && 
                  <Div key={index}style={{padding : 0}}>
                    {/* {awaitInfo && awaitInfo[category].length != 0 && <Header mode="secondary">{category}</Header> } */}
                    {awaitInfo && awaitInfo[category] && awaitInfo[category].map((item,index2) => {
                    if (item.taskIsEnd === "1"){
                      count += 1;
                      return <Div key={index+index2}style={{padding : 0}}>  
                                <Card size="l" mode="shadow" className="CardBody" onClick={()=> {props.setActivePanelTask("GiveFeedBack");
                                                                                                 props.setDataTransfer(item);
                                                                                                  }} >
                                  <Cell indicator={item.deadline} expandable >
                                    {item.name}
                                  </Cell>
                                </Card>
                              </Div>
                    }
                    })}
                  </Div>)
                  }
                  {tabsCount&& count === 0 && <Footer className="FooterTask">Пока пусто</Footer>}
                </div>
                 
               }
               
           </div>
           </Gallery>
         </Panel>
		
		
	
	);
};

Task.propTypes = {
};

export default Task;
