//https://task-today-tpuhack.herokuapp.com

const cancelTask = (props) => {
  fetch(`https://task-today-tpuhack.herokuapp.com/delete_my_task?id=id${props.id_v}&task_id=${props.dataTransfer._id}&category=${props.dataTransfer.categori}&url=${props.sign}`)
  .then(response => response.json())
  .then(result =>{
      switch (result){
        case 1 :
          alert("Задание удалено");
          props.setTabsCount(prevTabCount =>{
            var temp = prevTabCount;
            temp[1] -= 1;
            // console.log("GiveTask");
            // console.log(temp);
            // prevTabCount[1] += 1;
            return temp;
          })
          props.setActivePanelTask("Task");
        break;
        case 0 :
          alert("Произошла ошибка попробуйте позже");
        break;
      }
  });
  console.log("Cancel")
}

const AgreeWithTask = (props,person,setEmployeeInfo) =>{
  
  fetch(`https://task-today-tpuhack.herokuapp.com/agree_employer?task_id=${props.dataTransfer._id}&category=${props.dataTransfer.categori}&id=${person.vk_id}&url=${props.sign}`).then(response => response.json())
  .then(result =>{
    switch (result) {
      case 1 : 
        fetch(`https://task-today-tpuhack.herokuapp.com/get_employee?id=id${props.id_v}&url=${props.sign}`)
        .then(response => response.json())
        .then(result =>{
          props.setActivePanelTask("Task")
          if (result == "0") {
            } else {
              props.setCountActiveTask(result.active_task.length);
              props.setTabsCount(prevTabCount =>{
                var temp = prevTabCount;
                temp[0] += 1;
                return temp;
              })	
            }
          alert("Работник нанят");
        })
      break;
      case 0 : 
        alert("Работник не нанятб попробуйте ещ раз")
        props.setActivePanelTask("Task")
      break;
      default:
        alert("Error  Заявка неподана")
        props.setActivePanelTask("Task")
      break;
    }	
  })
}

