import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import io from 'socket.io-client';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Tabbar from '@vkontakte/vkui/dist/components/Tabbar/Tabbar'
import Icon28NewsfeedOutline from '@vkontakte/icons/dist/28/newsfeed_outline'
import Icon28DocumentOutline from '@vkontakte/icons/dist/28/document_outline';
import Root from '@vkontakte/vkui/dist/components/Root/Root'
import Epic from '@vkontakte/vkui/dist/components/Epic/Epic'
import Div from '@vkontakte/vkui/dist/components/Div/Div'
import Icon28UserOutline from '@vkontakte/icons/dist/28/user_outline'
import Icon28Notifications from '@vkontakte/icons/dist/28/notifications'
import Icon28More from '@vkontakte/icons/dist/28/more'
import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';
import Icon24Chats from '@vkontakte/icons/dist/24/chats';
import TabbarItem from '@vkontakte/vkui/dist/components/TabbarItem/TabbarItem'
import { Spinner } from '@vkontakte/vkui';


import '@vkontakte/vkui/dist/vkui.css';
import icon from './img/icon_150.png';

import FirstConnect from './panels/FirstConnect';
import Task from './panels/Task';
import Folowers from './panels/Folowers'
import Profile from './panels/Profile'
import ProfileEmployee from './panels/ProfileEmployee'
import ProfileTask from './panels/ProfileTask'
import GiveTask from './panels/GiveTask'
import GiveFeedBack from './panels/GiveFeedBack'
import GiveTaskEmployee from './panels/GiveTaskEmployee'
import Notifications from './panels/Notifications'
import AllTask from './panels/AllTask'
import AllEmployee from './panels/AllEmployee'
import ThematicsEmployee from './panels/ThematicsEmployee'
import ThematicsTask from './panels/ThematicsTask'
import Candidates from './panels/Candidates';
import Chats from './panels/Chats'


var chooseCard = []
var url = new URL(window.location.href);
const id_v = url.searchParams.get("vk_user_id");
const sign = String(window.location.search.slice(1).replace(/&/g,'%26'));
var token = null;
// const vk_app_id = url.searchParams.get("vk_app_id");
// const auth_key = url.searchParams.get("vk_auth_key")
// const sign = url.searchParams.get("sign");
// console.log(String(sign))
// console.log(url)
// console.log("SIGN "+sign);
// var socket = io.connect('http://localhost:8000');
// socket.emit("join",{id_v : id_v, url:url.href, sign: sign})
const App = () => {
	
	const [activePanelTask, setActivePanelTask] = useState("Task");
	const [activePanelAllTask, setActivePanelAllTask] = useState("AllTask");
	const [activePanelAllEmployee, setActivePanelAllEmployee] = useState("AllEmployee");
	const [dataTransfer, setDataTransfer] = useState(null);
	const [activeStory, setActiveStory] = useState("LoadingView");
	const [tabsCount, setTabsCount] = useState([0,0,0]);
	const [fetchedUser, setUser] = useState(null); 
	const [data, setData] = useState(null);
	const [countActiveTask, setCountActiveTask] = useState("0");
	const [popout, setPopout] = useState(null); //<ScreenSpinner size='large' />

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = 'client_light'; // data.scheme ? data.scheme : 
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		//https://cors-anywhere.herokuapp.com/
		async function fetchData2() {
			// setPopout(<ScreenSpinner size='large' />);
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			let response = await bridge.send("VKWebAppGetAuthToken", {"app_id": 7390067, "scope": ""});
			token = response.access_token;
			response = await fetch(`https://task-today-tpuhack.herokuapp.com/get_employee?id=id${id_v}&url=${sign}`);
			let result = await response.json();
			if (result == "0") {
				// console.log("open FirsConnect")
				setActiveStory("FirstConnect");
			  } else {
				// console.log("open Task")
				setCountActiveTask(result.active_task.length);
				setTabsCount(new Array(result.active_task.length,result.employer_task.length,result.await_task.length));
				setData(result);
				setActiveStory("Task");
				// setActiveStory("Profile");
				// setActiveStory("Chats");
				// setActiveStory("AllEmployee");
				// setActiveStory('AllTask');
				// setActiveStory('GiveTask');
			  }
			  
			// setPopout(null);
		}
		// fetchData();
		fetchData2();
	}, []);

	const next = e =>{
		setActiveStory(e.currentTarget.dataset.story);
		setActivePanelAllTask("AllTask");
		setActivePanelTask("Task");
		setActivePanelAllEmployee("AllEmployee")
	}
	return (
		
		<Epic activeStory={activeStory}  tabbar={((activeStory != "FirstConnect")&&(activeStory != "LoadingView")) ?(
			<Tabbar>
          <TabbarItem
            onClick={next}
            selected={activeStory === "Task"?1:null}
			data-story="Task"
            label={String(countActiveTask)}
            text="Задания"
          ><Icon28NewsfeedOutline /></TabbarItem>
          {/* <TabbarItem
            onClick={next}
            selected={activeStory}
            data-story="Chats"
            text="Чат"
          ><Icon24Chats /></TabbarItem> */}
          <TabbarItem
			onClick={next}
			// onClick={()=>alert("In Progress")}
            selected={activeStory === "Profile"?1:null}
            data-story="Profile"
            text="Профиль"
          ><Icon28UserOutline /></TabbarItem>
          {/* <TabbarItem
            onClick={next}
            selected={activeStory === "GiveTask"?1:null}
            data-story="GiveTask"
            text="Дать задание"
          ><Icon28AddOutline /></TabbarItem> */}
          <TabbarItem
			onClick={next}
			selected={activeStory === "AllEmployee"?1:null}
			data-story="AllEmployee"
            text="Вакансии"
          ><Icon28More /></TabbarItem>
		  <TabbarItem
            onClick={next}
			selected={activeStory === "AllTask"?1:null}
			data-story="AllTask"
            text="Работа"
          ><Icon28DocumentOutline /></TabbarItem>
        </Tabbar>):(<div></div>)
		}>	
			<div id="LoadingView" >
				{/* <div key="wwq"></div> */}
				<Panel className="GalleryBodyFirstConnect"  >
					<div className="GalleryBodyCenterPage1"  >
						<img className="IconFirstConnect" src={icon}  />
                		<div className="TextFirstConnect"  >TaskToday</div>
                		<div className="Text2FirstConnect"  >Работа у вас под рукой</div>
						{/* <div className="Text2FirstConnect" style={{color: "red", fontSize: 25, fontWeight: "bold"}} >Ведутся тех. работы</div> */}
						<Spinner size="regular"   style={{ paddingTop: 20,paddingBottom: 20 }} />
					</div>
				</Panel>
				
				{/* <Panel >
					<ScreenSpinner  size='large' style={{position:"absolute",}}/>
				</Panel> */}
				
			</div>
			{/* <Root id="FirstConnect"> */}
			<FirstConnect id='FirstConnect' fetchedUser={fetchedUser} setActiveStory={setActiveStory} id_v={id_v} setPopout={setPopout}
								sign={sign}></FirstConnect>
			{/* </Root> */}
			<View id="Task" activePanel={activePanelTask} popout={popout}>

				<Task id='Task' fetchedUser={fetchedUser} id_v={id_v} setActivePanelTask = {setActivePanelTask} 
						setDataTransfer={setDataTransfer} setPopout={setPopout} setData={setData}
						tabsCount={tabsCount} sign={sign}/>
				<ProfileTask id='ProfileEmployee' fetchedUser={fetchedUser} id_v={id_v} token={token} setActivePanelTask={setActivePanelTask}   
								 setPopout={setPopout} data={dataTransfer} setDataTransfer={setDataTransfer} sign={sign}/>
				<Candidates id='Candidates' setActivePanelTask = {setActivePanelTask} setDataTransfer={setDataTransfer}
							dataTransfer = {dataTransfer} setTabsCount={setTabsCount}
							setPopout={setPopout} setCountActiveTask={setCountActiveTask} id_v={id_v} token={token} sign={sign} />
				<GiveFeedBack id='GiveFeedBack' setActivePanelTask = {setActivePanelTask} setDataTransfer={setDataTransfer}
							dataTransfer = {dataTransfer} setTabsCount={setTabsCount} setActiveStory={setActiveStory}
							setPopout={setPopout} setCountActiveTask={setCountActiveTask} id_v={id_v} token={token} sign={sign} />
				<Chats id='Chats' setPopout={setPopout} setActiveStory={setActiveStory} setActivePanelTask = {setActivePanelTask} dataTransfer={dataTransfer}
								  id_v={id_v} sign={sign}/>
				
			</View>
			<View id="Chats" activePanel="Chats" popout={popout}>
				<Chats id='Chats' fetchedUser={fetchedUser} setPopout={setPopout} setActiveStory={setActiveStory}
								  id_v={id_v}/>
			</View>
			<View id="Profile" activePanel="Profile" popout={popout}>
				<Profile id='Profile' fetchedUser={fetchedUser} id_v={id_v} setPopout={setPopout} data={data} sign={sign}/>
			</View>
			{/* <View id="GiveTask" activePanel="GiveTask" popout={popout}>
				<GiveTask id='GiveTask' fetchedUser={fetchedUser} id_v={id_v} setPopout={setPopout} setActiveStory={setActiveStory} sign={sign}/>
			</View> */}
			<View id="AllTask" activePanel={activePanelAllTask} popout={popout}>
				<AllTask id='AllTask' fetchedUser={fetchedUser}  setActiveStory={setActiveStory} setActivePanelAllTask={setActivePanelAllTask}   
						 setDataTransfer={setDataTransfer} id_v={id_v} setPopout={setPopout} sign={sign}/>
				<ThematicsTask id="ThematicsTask" setActivePanelAllTask={setActivePanelAllTask} 
							   id_v={id_v} setActiveStory={setActiveStory} dataTransfer = {dataTransfer} setTabsCount={setTabsCount}
							   dataTransfer = {dataTransfer} setPopout={setPopout} sign={sign}></ThematicsTask>
				<GiveTask id='GiveTask' fetchedUser={fetchedUser} id_v={id_v} setPopout={setPopout} setActivePanelAllTask={setActivePanelAllTask}
										 setActiveStory={setActiveStory} setTabsCount={setTabsCount} sign={sign}/>
			</View>
			<View id="AllEmployee" activePanel={activePanelAllEmployee} popout={popout}>
				<AllEmployee id='AllEmployee' fetchedUser={fetchedUser}  setActiveStory={setActiveStory} setActivePanelAllEmployee={setActivePanelAllEmployee}   
						setDataTransfer={setDataTransfer} id_v={id_v} setPopout={setPopout} sign={sign}/>
				<ThematicsEmployee id="ThematicsEmployee" setActivePanelAllEmployee={setActivePanelAllEmployee} 
							   id_v={id_v} setActiveStory={setActiveStory} setDataTransfer={setDataTransfer}
							   dataTransfer = {dataTransfer} setPopout={setPopout} token={token} sign={sign}></ThematicsEmployee>
				<ProfileEmployee id='ProfileEmployee' fetchedUser={fetchedUser} id_v={id_v} token={token} setActivePanelAllEmployee={setActivePanelAllEmployee}   
								 setPopout={setPopout} data={dataTransfer} setDataTransfer={setDataTransfer} sign={sign}/>
				<GiveTaskEmployee id='GiveTaskEmployee' fetchedUser={fetchedUser} id_v={id_v} setPopout={setPopout} setActivePanelAllEmployee={setActivePanelAllEmployee}
										data={dataTransfer} setActiveStory={setActiveStory} sign={sign}/>
				
							   
			</View>

		</Epic>
		
	);
}

export default App;

