import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import Textarea from '@vkontakte/vkui/dist/components/Textarea/Textarea'
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import FixedLayout from '@vkontakte/vkui/dist/components/FixedLayout/FixedLayout'
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import List from "@vkontakte/vkui/dist/components/List/List"
import Checkbox from '@vkontakte/vkui/dist/components/Checkbox/Checkbox'
import Input from '@vkontakte/vkui/dist/components/Input/Input'
import Select from '@vkontakte/vkui/dist/components/Select/Select'
import Search from "@vkontakte/vkui/dist/components/Search/Search"

import '../css/GiveTask.css'

import { FormLayout,FormStatus } from '@vkontakte/vkui';
import { Alert } from '@vkontakte/vkui';




const GiveTask = (props) => {        
    window.scrollTo(0, 0);
    const thematics = [
        {id: 1, name: "Программирование"},
        {id: 2, name: "Доставка"},
        {id: 3, name: "Учёба"},
        {id: 4, name: "Дизайн"},
        {id: 5, name: "Пение"},
        {id: 6, name: "Готовка"},
        {id: 7, name: "Minecraft"},
        {id: 8, name: "Общение"},
        {id: 9, name: "Рисование"},
        {id: 10, name: "Скульптура"},
        {id: 11, name: "Прочее"},        
      ];

    const [search, SetSearch] = useState("");
    const [nameStatus, setNameStatus] = useState("default");
    const [aboutStatus, setAboutStatus] = useState("default");
    const [deadLineStatus, setdeadLineStatus] = useState("default");
    const [thematicStatus, setThematicStatus] = useState("default");
    const [priceStatus, setPriceStatus] = useState("default");
    const [mainStatus, setMainStatus] = useState(null);
    // var name = null;
        // const about = null;
        // const deadLine = document.getElementById("Dead-line").value;
        // const thematic = document.getElementById("Thematic").value;
        // const price = document.getElementById("TaskPrice").value;

    const onChange = (e) =>
    {
        SetSearch(e.target.value);        
    }   

    const onClick = (e) =>
    {        
        createTask();    
      //   props.setPopout(
      //       <Alert
      //   actions={[{
      //     title: 'Отмена',
      //     autoclose: true,
      //     mode: 'cancel'
      //   }, {
      //     title: 'Добавить',
      //     autoclose: true,
      //   }]}
      // >          
      //   <h2>Подтвердите действие</h2>
      //   <p>Добавить пользователю право на модерацию контента.</p>
      // </Alert>
      //   ); 
    }

    const createTask = ()=>
    {   
        props.setPopout(<ScreenSpinner size='large' />);
        const name = document.getElementById("TaskName").value;
        const about = document.getElementById("About").value;
        const deadLine = document.getElementById("Dead-line").value;
        const thematic = document.getElementById("Thematic").value;
        const price = document.getElementById("TaskPrice").value;
        setNameStatus(name?"defualt":"error");
        setAboutStatus(about?"defualt":"error");
        setdeadLineStatus(deadLine?"defualt":"error");
        setThematicStatus(thematic?"defualt":"error");
        setPriceStatus(price?"defualt":"error");
        if (!name | !about | !deadLine | !thematic | !price) setMainStatus(1);
        // console.log(thematic, thematics.find(({id}) =>{return id == thematic}).name);
        props.setPopout(null);
        //https://task-today-tpuhack.herokuapp.com
        if (name && about && deadLine && thematic && price){
        console.log("startGetTask")
        fetch(`https://task-today-tpuhack.herokuapp.com/create_task?name=${name}&about=${about}&categori=${thematics.find(({id}) =>{return id == thematic}).name}&price=${price}&id=id${props.id_v}&deadline=${deadLine}&url=${props.sign}`)
    		.then(response => response.json())
    		.then(result =>{
        	try{
                console.log(result)
				if (result == "0") {
          // console.log(thematics.find(({id}) =>{return id == thematic}).name);
					alert("Ошибка подачи задания");
				}else{
                    // alert("Задание созданно")
                    fetch(`https://task-today-tpuhack.herokuapp.com/set_new_task?task_id=${result}&id=id${props.id_v}&url=${props.sign}`)
    		        .then(response => response.json())
    		        .then(result2 =>{  
                        props.setPopout(null); 
                        props.setTabsCount(prevTabCount =>{
                            var temp = prevTabCount;
                            temp[1] += 1;
                            // console.log("GiveTask");
                            // console.log(temp);
                            // prevTabCount[1] += 1;
							return temp;
						})	
                        alert("Задание подано")
                        props.setActiveStory("Task")                         
                        console.log(result2);
                    });				
				}				
            }
            catch(err){props.setPopout(null); console.log(err);
        }});
        }
    }    
    return(
        <Panel id={props.id}>
        
		<PanelHeader left={<PanelHeaderButton onClick={() => {props.setActivePanelAllTask("AllTask")}}><Icon24Back/></PanelHeaderButton>} className='PanelHeader'>Дать задание</PanelHeader>
        <FormLayout>
            {mainStatus&&
                <FormStatus header="Ошибка ввода" mode={"error"}>
                    Необходимо корректно ввести данные
                </FormStatus> 
            }    
            {/* <div className="MainText" >
		        <div >Укажите ваше задание</div>
            </div>             */}
            <Input id="TaskName" autoComplete="off" top = "Название" maxLength={30} type="text" defaultValue=""
              status={nameStatus}
              bottom={nameStatus === "error" ? 'Имя задание введено неверно!' : 'Пожалуйста, введите название задания, всего 30 символов'}/>
            <Textarea className="About" id="About" top="О задании" maxLength={150}
             status={aboutStatus}
             bottom={aboutStatus === "error" ? 'Описание задания введено неверно!' : 'Пожалуйста, введите описание задания, всего 150 символов'}></Textarea>
             
            <Input autoComplete="off" id="Dead-line" top = "Дедлайн" type="date" defaultValue="" status={deadLineStatus}/>                       
            <Select id="Thematic" autoComplete="off" placeholder="Категория" status={thematicStatus}>
                {thematics.map(thematic => <option key={thematic.id} value={thematic.id}>{thematic.name}</option>)}                
            </Select>                              
            <Input id="TaskPrice" autoComplete="off" top = "Вознаграждение" type="number" defaultValue="" status={priceStatus}/>
            <Button size="xl" onClick={()=>createTask()} status="default">Добавить</Button>  
        </FormLayout>
        
        
	</Panel>
    );
	
};

GiveTask.propTypes = {
	
};

export default GiveTask;
