import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import Textarea from '@vkontakte/vkui/dist/components/Textarea/Textarea'
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import FixedLayout from '@vkontakte/vkui/dist/components/FixedLayout/FixedLayout'
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import List from "@vkontakte/vkui/dist/components/List/List"
import Checkbox from '@vkontakte/vkui/dist/components/Checkbox/Checkbox'
import Input from '@vkontakte/vkui/dist/components/Input/Input'
import Select from '@vkontakte/vkui/dist/components/Select/Select'
import Search from "@vkontakte/vkui/dist/components/Search/Search"

import '../css/GiveTask.css'

import { FormLayout,FormStatus } from '@vkontakte/vkui';
import { Alert } from '@vkontakte/vkui';




const GiveFeedBack = (props) => {        
    window.scrollTo(0, 0);
    const thematics = [
        {id: 1, name: "Программирование"},
        {id: 2, name: "Доставка"},
        {id: 3, name: "Учёба"},
        {id: 4, name: "Дизайн"},
        {id: 5, name: "Пение"},
        {id: 6, name: "Готовка"},
        {id: 7, name: "Minecraft"},
        {id: 8, name: "Общение"},
        {id: 9, name: "Рисование"},
        {id: 10, name: "Скульптура"},
        {id: 11, name: "Прочее"},        
      ];

    const [search, SetSearch] = useState("");
    const [aboutStatus, setAboutStatus] = useState("default");
    const [mainStatus, setMainStatus] = useState(null);
    // var name = null;
        // const about = null;
        // const deadLine = document.getElementById("Dead-line").value;
        // const thematic = document.getElementById("Thematic").value;
        // const price = document.getElementById("TaskPrice").value;

    const onChange = (e) =>
    {
        SetSearch(e.target.value);        
    }   

    const onClick = (e) =>
    {        
        createTask();    
      //   props.setPopout(
      //       <Alert
      //   actions={[{
      //     title: 'Отмена',
      //     autoclose: true,
      //     mode: 'cancel'
      //   }, {
      //     title: 'Добавить',
      //     autoclose: true,
      //   }]}
      // >          
      //   <h2>Подтвердите действие</h2>
      //   <p>Добавить пользователю право на модерацию контента.</p>
      // </Alert>
      //   ); 
    }

    const createTask = ()=>
    {   
        props.setPopout(<ScreenSpinner size='large' />);
        const about = document.getElementById("About").value;
        console.log(props.dataTransfer)
        setAboutStatus(about?"defualt":"error");
        if (!about ) setMainStatus(1);
        props.setPopout(null);
        //https://task-today-tpuhack.herokuapp.com
        if (about){
        console.log("startGetTask")
        fetch(`https://task-today-tpuhack.herokuapp.com/add_feedback?id=id${props.id_v}&feedback=${about}&id_employee=${props.dataTransfer.id_employee}&task_id=${props.dataTransfer._id}&category=${props.dataTransfer.categori}&url=${props.sign}`)
    		.then(response => response.json())
    		.then(result =>{
        	try{
                console.log(result)
				if (result == "0") {
          // console.log(thematics.find(({id}) =>{return id == thematic}).name);
					alert("Ошибка подачи отзыва");
				}else{
                    // alert("Задание созданно")
                        alert("Отзыв подан");
                        props.setPopout(null);
                        props.setTabsCount(prev =>{
                            var temp = prev;
                            temp[2] -= 1;
							return temp;
                        })
                        props.setActivePanelTask("Task");                  
			
				}				
            }
            catch(err){props.setPopout(null); console.log(err);
        }});
        }
    }    
    return(
        <Panel id={props.id}>
        
		<PanelHeader left={<PanelHeaderButton onClick={() => {props.setActivePanelTask("Task")}}><Icon24Back/></PanelHeaderButton>} className='PanelHeader'>Оставить отзыв</PanelHeader>
        <FormLayout>
            {mainStatus&&
                <FormStatus header="Ошибка ввода" mode={"error"}>
                    Необходимо корректно ввести данные
                </FormStatus> 
            }    
            {/* <div className="MainText" >
		        <div >Укажите ваше задание</div>
            </div>             */}
            <Textarea className="About" id="About" top="Отзыв" maxLength={50}
             status={aboutStatus}
             bottom={aboutStatus === "error" ? 'Отзыв введен неверно!' : 'Пожалуйста, введите отзыв, всего 50 символов'}></Textarea>
             
            <Button size="xl" onClick={()=>createTask()} status="default">Добавить</Button>  
        </FormLayout>
        
        
	</Panel>
    );
	
};

GiveFeedBack.propTypes = {
	
};

export default GiveFeedBack;
